import * as Yup from "yup";

//validacion login
export const validationSchemaLogin = Yup.object().shape({
  correo: Yup.string()
    .email("Ingresa un correo valido")
    .required("Obligatorio"),
  contrasea: Yup.string().required("Obligatorio"),
});

export const initialValuesLogin = {
  correo: "",
  contrasea: "",
};

//validacion registro
export const initialValuesRegistro = {
  Nombre: "",
  Telefono: "",
  Email: "",
  Contrasea: "",
  ConfirmarContrasea: "",
};

export const validationSchemaRegistro = Yup.object().shape({
  Nombre: Yup.string().min(3, "Demasiado corto").required("Obligatorio"),
  Telefono: Yup.number()
    .typeError("Ingresa un telefono valido")
    .required("Obligatorio"),
  Email: Yup.string().email("Ingresa un correo valido").required("Obligatorio"),
  Contrasea: Yup.string()
    .min(8, "El tamao minimo de la contrasea debe ser 8 caracteres")
    .required("Obligatorio"),
  ConfirmarContrasea: Yup.string()
    .oneOf([Yup.ref("Contrasea")], "Las contraseas no coinciden")
    .required("Obligatorio"),
});
