import axios from "axios";

const URL = "https://3.83.218.170:4000/api/auth/login";

export default function loginService({ correo, contrasea }) {
  axios.defaults.withCredentials = true;
  return axios
    .post(URL, { correo, contrasea }, { withCredentials: true })
    .then((res) => {
      const { token } = res.data;
      return token;
    });
}
